import Vue from 'vue';
import axios from 'axios';
import QS from 'qs';
import store from '../store/index';
import router from '@/router/index'

// 
if (process.env.NODE_ENV == 'development') {
    axios.defaults.baseURL = 'http://192.168.38.100:8088';
} else if (process.env.NODE_ENV == 'debug') {
    axios.defaults.baseURL = 'http://192.168.38.100:8088';
} else if (process.env.NODE_ENV == 'production') {
    axios.defaults.baseURL = 'http://192.168.38.100:8088';
}

//
axios.defaults.timeout = 10000;

// axios.defaults.withCredentials = true;

//post
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

// 
axios.interceptors.request.use(
    config => {
        // tokenhttpheadertoken
        // tokentoken
        const token = store.state.token;
        token && (config.headers.Authorization = token);
        return config;
    },
    error => {
        return Promise.error(error);
    })

// 
axios.interceptors.response.use(
    response => {
        if (response.status === 200) {
            //401403500{"code":401,"msg":"","data":null,"success":false}
            interceptHttpCode200Business(response);

            return Promise.resolve(response);
        } else {
            return Promise.reject(response);
        }
    },
    // 200
    error => {
        if (error.response.status) {
            interceptHttpCodeAndBusinessCode(error.response.status,error.response.data.message);
            return Promise.reject(error.response);
        }
    }
);
/**
 * 
 * @param {Integer} code [http status or code]
 * @param {String} msg []
 */
function interceptHttpCodeAndBusinessCode(code,msg) {
    switch (code) {
        // 401: 
        // 
        // 
        case 401:
            // token
            store.commit('clearLogin');
            router.replace({
                path: '/signin',
                query: { redirect: router.currentRoute.fullPath }
            });
            break;
            // 403 token
            // 
            // tokenvuextoken
            // 
        case 403:
            Toast({
                message: '',
                duration: 1000,
                forbidClick: true
            });
            // token
            store.commit('clearLogin');
            // fullPath
            setTimeout(() => {
                router.replace({
                    path: '/signin',
                    query: {
                        redirect: router.currentRoute.fullPath
                    }
                });
            }, 1000);
            break;
            // 404
        case 404:
            Toast({
                message: '',
                duration: 1500,
                forbidClick: true
            });
            break;
            // 
        default:
            Toast({
                message: msg,
                duration: 1500,
                forbidClick: true
            });
        }
}

function interceptHttpCode200Business(res) {
    if (res.data.code === 401 || res.data.code === 403 || res.data.code === 404 || res.data.code === 500) {
        interceptHttpCodeAndBusinessCode(res.data.code,res.data.msg);
    }
}

/**
 * getget
 * @param {String} url [url]
 * @param {Object} params []
 */
export function get(url, params) {
    return new Promise((resolve, reject) => {
        axios.get(url, {
            params: params,
            headers:{
                'Authorization': localStorage.getItem('Authorization')
            }
        }).then(res => {
            resolve(res.data);
        }).catch(err => {
            reject(err.data)
        })
    });
}

/**
 * postpost
 * @param {String} url [url]
 * @param {Object} params []
 */
export function post(url, params) {
    return new Promise((resolve, reject) => {
        axios.post(url, params,{headers: {
            'Authorization': localStorage.getItem('Authorization'),
        }})
            .then(res => {
                resolve(res.data);
            })
            .catch(err =>{
                reject(err.data)
            })
    });
}
